
import { useRoute, useRouter } from "vue-router";
import { computed, defineComponent, reactive, ref, toRefs, watch } from "vue";
import { AxiosResponseProps } from "@/utils/request";
import { useStore } from "vuex";
import useClickDocument, { useDocumentContextMenu } from "@/hooks/useClickDocument";
import { getDoucmentHeight } from "@/utils/document";
import { deletePlayList } from "@/api/playList";
import { copyValue } from "@/utils/document";
import { getIntelligenceListApi } from "@/api/common";
import { getRandomInt } from "@/utils/math";
import { isEmptyObject } from "@/utils/obj";
import { addHaveUrl } from "@/hooks/useFilters";
import { getPlayListDetailApi } from "@/api/playList";
export default defineComponent({
    setup() {
        const router = useRouter();
        const route = useRoute();
        const store = useStore();
        const userId = computed(() => store.state.userInfo.userId);
        let initialMenuList: { title: string; key?: string; active?: boolean; contextMenuActive?: boolean; path?: string; id?: number }[] = [
            {
                title: "发现音乐",
                key: "fxyy",
                active: true,
                path: "/container/discoverMusic",
            },
            {
                title: "视频",
                path: "/container/videos",
                key: "sp",
            },
            {
                title: "朋友",
                key: "py",
                path: "/container/friends",
            },
            {
                title: "直播",
                key: "zb",
            },
            {
                title: "私人FM",
                key: "srfm",
            },
        ];
        const initMenuList = () => {
            const path = route.path;
            if (!initialMenuList.find(item => item.path == path)) return;
            initialMenuList = initialMenuList.map(item => (item.path == path ? { ...item, active: true } : { ...item, active: false }));
        };
        initMenuList();
        const state = reactive({
            createdCollapsed: false,
            collectedCollapsed: false,
            canMyLikeHover: true,
            addListModalVisible: false,
            addMusicListObj: {
                name: "",
                isPrivate: false,
            },
            commonToastRef: ref(null),
            createdMusicList: computed(() => store.state.createdMusicList.filter(item => item.creator.userId === userId.value)),
            collectedMusicList: computed(() => store.state.createdMusicList.filter(item => item.creator.userId !== userId.value)),
            menuList: [...initialMenuList],
            contextMenuPageX: 0,
            contextMenuPageY: 0,
            showContextMenu: false,
            contextIsUp: true,
            documentClientHeight: 0,
            contextMenuMuisicListItem: { id: null },
            showCommonModal: false,
            forbidMenuList: [],
            loginStatus: computed(() => store.getters.getLoginStatus),
            contextMenuList: [
                {
                    operation: "play",
                    name: "播放",
                    iconWidth: "21",
                    iconHeight: "21",
                },
                {
                    operation: "downloadAll",
                    name: "下载全部",
                    iconWidth: "18",
                    iconHeight: "18",
                },
                {
                    operation: "copyLink",
                    name: "复制链接",
                    iconWidth: "18",
                    iconHeight: "18",
                },
                {
                    operation: "editInfo",
                    name: "编辑歌单信息",
                    iconWidth: "16",
                    iconHeight: "16",
                },
                {
                    operation: "delete",
                    name: "删除歌单",
                    iconWidth: "16",
                    iconHeight: "16",
                },
            ],
        });
        watch(
            () => state.createdMusicList,
            newV => {
                if (newV instanceof Array) {
                    const listId = route.query.id;
                    state.menuList = [
                        ...(listId ? initialMenuList.map(item => ({ ...item, active: false })) : initialMenuList),
                        ...newV.map(item => ({ ...item, active: item.id == listId, key: item.id, type: "created" })),
                        ...state.collectedMusicList.map(item => ({ ...item, active: item.id == listId, key: item.id, type: "collected" })),
                    ];
                }
            }
        );
        const clickMenuItem = (menuItem, isHeartbeat = false) => {
            if (isEmptyObject(menuItem)) {
                return store.commit("changeLoginModalVisible", true);
            }
            //心动模式
            if (isHeartbeat === true) {
                let favaratedListObj = state.menuList[5];
                if (favaratedListObj) {
                    getPlayListDetailApi({ id: favaratedListObj.id, t: Date.now() }).then((res: any) => {
                        if (res.code === 200 && res.playlist?.tracks) {
                            getIntelligenceListApi({ pid: favaratedListObj.id, id: res.playlist.tracks[0].id }).then(
                                async (res1: AxiosResponseProps) => {
                                    if (res1.code === 200) {
                                        let randomMusicIndex = getRandomInt(0, res1.data.length - 1);
                                        store.commit("changeCurrentMusicDetail", res1.data[randomMusicIndex].songInfo);
                                        store.commit("changeCurrentPlayList", [
                                            ...(await addHaveUrl(res1.data)).reduce((pre, item) => [...pre, item.songInfo], []),
                                            ...store.state.currentPlayList,
                                        ]);
                                        store.dispatch("getCurrentMusicUrlInfo", { id: res1.data[randomMusicIndex].songInfo.id });
                                        state.commonToastRef.heartbeat("已开启心动模式");
                                    }
                                }
                            );
                        }
                    });
                }
                return;
            }
            if (menuItem.id !== undefined) {
                return router.push({ path: "/container/createdMusicList", query: { id: menuItem.id, timeStamp: Date.now() } });
            }
            if (menuItem.path) {
                router.push(menuItem.path);
            }
        };
        const getCreatedMusicList = () => {
            store.dispatch("getCreatedMusicList");
        };
        getCreatedMusicList();
        watch(
            () => store.getters.getLoginStatus,
            status => {
                getCreatedMusicList();
            }
        );
        const completeCreate = () => {
            state.commonToastRef.success("创建歌单成功");
        };
        const stopDefaultEvent = event => {
            state.showContextMenu = true;
            // event.preventDefault ? event.preventDefault() : (event.returnValue = false); //阻止默认行为
            // event.cancelBubble ? (event.cancelBubble = true) : event.stopPropagation; //阻止冒泡
            state.contextMenuPageX = event.pageX;
            state.contextMenuPageY = event.pageY;
            state.documentClientHeight = getDoucmentHeight();
            if (event.pageY > state.documentClientHeight / 2) {
                state.contextIsUp = true;
            } else {
                state.contextIsUp = false;
            }
        };
        const handleMusicListContextMenu = (event, menuItem, forbidMenuList) => {
            if (forbidMenuList && forbidMenuList.length) {
                state.forbidMenuList = forbidMenuList;
            } else {
                state.forbidMenuList = [];
            }
            state.menuList = state.menuList.map(item => ({ ...item, contextMenuActive: false }));
            state.menuList.find(item => item.id == menuItem.id).contextMenuActive = true;
            stopDefaultEvent(event);
            state.contextMenuMuisicListItem = menuItem;
        };
        //点击鼠标左键
        useClickDocument(() => {
            state.showContextMenu = false;
            state.menuList = state.menuList.map(item => ({ ...item, contextMenuActive: false }));
        });
        //点击鼠标右键
        useDocumentContextMenu(() => {
            state.showContextMenu = false;
            state.menuList = state.menuList.map(item => ({ ...item, contextMenuActive: false }));
        });
        const clickContextMenu = type => {
            switch (type) {
                case "play":
                    state.menuList = state.menuList.map(item => ({ ...item, active: item.id === state.contextMenuMuisicListItem.id }));
                    router.push({
                        path: "/container/createdMusicList",
                        query: { id: state.contextMenuMuisicListItem.id, immediate: "true", timeStamp: Date.now() },
                    });
                    break;
                case "downloadAll":
                    state.menuList = state.menuList.map(item => ({ ...item, active: item.id === state.contextMenuMuisicListItem.id }));
                    router.push({
                        path: "/container/createdMusicList",
                        query: { id: state.contextMenuMuisicListItem.id, download: "true" },
                    });
                    break;
                case "copyLink":
                    let musicListId = state.contextMenuMuisicListItem.id;
                    let userId = store.state.userInfo.userId;
                    copyValue(`http://music.163.com/playlist?id=${musicListId}&userid=${userId}`);
                    state.commonToastRef.success("链接复制成功");
                    break;
                case "editInfo":
                    router.push({ path: "/container/updateMusicList", query: { id: state.contextMenuMuisicListItem.id } });
                    break;
                case "delete":
                    state.showCommonModal = true;
                    break;
            }
            state.showContextMenu = false;
        };
        const confirmCommonModal = () => {
            deletePlayList({ id: state.contextMenuMuisicListItem.id }).then(async (res: AxiosResponseProps) => {
                if (res.code === 200) {
                    await store.dispatch("getCreatedMusicList");
                    state.commonToastRef.success("删除成功");
                } else {
                    state.commonToastRef.error(res.msg || "删除失败，请稍后重试");
                }
            });
            state.showCommonModal = false;
        };
        return {
            ...toRefs(state),
            clickMenuItem,
            completeCreate,
            window,
            handleMusicListContextMenu,
            clickContextMenu,
            confirmCommonModal,
        };
    },
});
